.flexbox {
  display: flex;
}

@for $i from 1 through 20 {
  .gap-#{$i} {
    gap: #{$i}px;
  }
}

.flex-center {
  justify-content: center;
  align-items: center;
}

.flex-column {
  flex-direction: column;
}

.flex-align-center {
  align-items: center;
}

.flex-justify-center {
  justify-content: center;
}

.flex-align-end {
  align-items: flex-end;
}

.flex-justify-end {
  justify-content: flex-end;
}

.flex-wrap {
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

%reset-appearance {
  appearance: none;
  border: none;
  outline: none;
  background: transparent;
}

.bold {
  font-weight: 600;
}

.rounded {
  border-radius: 9999px;
}

@for $i from 0 through 20 {
  .p-#{$i} {
    padding: #{$i}px;
  }
  .px-#{$i} {
    padding: 0 #{$i}px;
  }
  .py-#{$i} {
    padding: #{$i}px 0;
  }
  .pl-#{$i} {
    padding-left: #{$i}px;
  }
  .pr-#{$i} {
    padding-right: #{$i}px;
  }
}

@for $i from 0 through 20 {
  .m-#{$i} {
    margin: #{$i}px;
  }
  .ml-#{$i} {
    margin-left: #{$i}px;
  }
  .mr-#{$i} {
    margin-right: #{$i}px;
  }
  .mt-#{$i} {
    margin-top: #{$i}px;
  }
  .mb-#{$i} {
    margin-bottom: #{$i}px;
  }
}

.ml-auto {
  margin-left: auto;
}

@for $i from 1 through 9 {
  .font-0#{$i} {
    font-size: calc(#{$i}em / 10);
  }
  .font-1#{$i} {
    font-size: calc(1#{$i}em / 10);
  }
  .font-2#{$i} {
    font-size: calc(2#{$i}em / 10);
  }
  .font-3#{$i} {
    font-size: calc(3#{$i}em / 10);
  }
}

.full-height {
  height: 100%;
}

.full-width {
  width: 100%;
}

.card {
  background-color: white;
  padding: 32px;
  box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  height: fit-content;
  width: fit-content;
}

.hidden {
  display: none !important;
}
